
.container {
	margin: 0 auto;
	min-width: 320px;
	padding-left: 15px;
	padding-right: 15px;
	width: 100%;
}

.text-input {
	border: none;
	outline: none !important;
	-webkit-appearance: none;
	background: none;
	overflow: hidden;

	height: 65px;
	line-height: normal;
	color: #000;
	font-weight: 400;
	font-size: 16px;
	font-family: 'ProximaNova'; // SET FONT FAMILY
	
	border-radius: 3px;
	
	border: 1px solid #ccc;
	

	padding-left: 20px;
	padding-right: 0;

	width: 100%;
	background-color: #fff;

	&:-moz-placeholder {
		color: #879ea7;
	}
	&::-moz-placeholder { 
		color: #879ea7;
	}
	&:-ms-input-placeholder { 
		color: #879ea7;
	}
	&::-webkit-input-placeholder {
		color: #879ea7;
	}
}

.btn {
	position: relative;
	outline: none !important;
	border: none;
	cursor: pointer;
	text-decoration: none !important;
	background: none;
	-webkit-appearance: none;
	
	display: block;
	width: 240px;
	
	background-color: $brand-color;
	
	text-align: center;
	border-radius: 0;
	color: #fff;
	
	text-transform: uppercase;

	font-family: 'OpenSans', sans-serif;
	font-size: 16px;
	letter-spacing: 0.025em;
	font-weight: 700;
	
	-webkit-transform: translateZ(0px);
	-webkit-font-smoothing: antialiased;
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;

	height: 60px;
	line-height: 60px;
	
	border: none;
	
	vertical-align: middle;
	
	transition: all 0.2s ease-in-out;
	
	box-shadow: 0px 0px 26.7px 3.3px rgba(255, 152, 0, 0.34);
	
	> * {
		line-height: 1;
	}
	
	&:hover {
		background-color: lighten($brand-color, 5%);
		box-shadow: none;

	}
	
	&:active {
		transform: translateY(3px);
		box-shadow: none;
		background-color: darken($brand-color, 10%);
	}
	
	&:active,
	&:focus,
	&:hover {
		color: #fff;
		text-decoration: none !important;
		outline: none !important;
	}
	
	&:hover {
		color: #000;
	}
	
}



@media ($mobile) {
	.container {
		max-width: 500px;
	}
}

@media ($tablet) {
	.container {
		max-width: 900px;
		padding-left: 30px;
		padding-right: 30px;
	}
}

@media ($desktop) {
	.container {
		width: 1170px;
	}
}


